<script>
import imgs from '../components/detail/imgs'
import FlashSaleBar from '../components/detail/flashsalebar'
import FlashOrderBar from '../components/detail/bar'
import PresaleBar from '../components/detail/presalebar'
export default {
    name:'detail',
    metaInfo(){
        return {
            title:this.pageTitle
        }
    },
    components:{
        imgs,
        FlashSaleBar,
        FlashOrderBar,
        PresaleBar,
    },
    data(){
        return{
            product_type:0,
            step:1
        }
    },
    computed:{
        progressWidth(){
            return this.step===1&&'0%'||this.step===2&&'50%'||this.step===3&&'100%'
        },
        pageTitle(){
            return '商品详情-福分通内购'
        }
    }
}
</script>
<template>
    <div class="page-detail">
        <imgs></imgs>
        <template v-if="product_type===1">
            <flash-sale-bar></flash-sale-bar>
        </template>
        <template v-else-if="product_type===0">
            <presale-bar></presale-bar>
        </template>
        <div class="page-detail-title">
            <div >
                <img src="/static/imgs/tag@2x.png" alt="">
            </div>
            <div class="page-detail-title_name">
                网易智造扫拖两用机网易智造扫拖两用机网易智造扫拖两用机网易智造扫拖两用机
            </div>
        </div>
        <div class="page-detail-deposit" v-if="product_type===0">
            定金
            <span>￥100</span>
        </div>
        <div class="page-detail-stepline" v-if="product_type===0">
            <div :class="['page-detail-stepline_step',step>=1&&'active-step']">
                <span class="deposit">￥100</span>
                <span class="maru maru_1"></span>
                <span class="desc">定金</span>
            </div>
            <div :class="['page-detail-stepline_step',step>=2&&'active-step']">
                <span class="deposit">￥200</span>
                <span class="maru maru_2"></span>
                <span class="desc">定金翻倍</span>
            </div>
            <div :class="['page-detail-stepline_step',step>=3&&'active-step']">
                <span class="deposit">￥1700</span>
                <span class="maru maru_3"></span>
                <span class="desc">尾款</span>
            </div>
            <div class="page-detail-stepline_faultline">
                <div 
                class="progress"
                :style="{width:progressWidth}"
                ></div>
            </div>
        </div>
        <flash-order-bar></flash-order-bar>
    </div>
</template>
<style lang="stylus" scoped>
.page-detail
    position relative
    &-title 
        height .8rem
        background-color #fff
        padding 0.2rem .25rem
        display flex 
        img 
            display block 
            width .8rem 
            height .4rem
        &_name 
            width 6rem
            line-height .4rem
            margin-left .1rem
            white-space nowrap
            overflow hidden
            text-overflow ellipsis
    &-deposit
        padding 0 .25rem
        color #666
        &>span 
            color #f07
            font-size .4rem
            font-weight bold
    &-stepline
        position relative
        padding 0 .25rem
        height 1rem
        text-align center
        display flex 
        justify-content space-between
        color #666
        margin .2rem 0
        &_step
            display flex 
            flex-direction column
            &>span
                line-height 1.6
            &>.maru:after
                content ""
                display block
                box-sizing border-box
                width .2rem 
                height .2rem
                border-radius 50%
                background-color #CDCACA
                border 2px solid #CDCACA
            &>.maru_2:after
                margin  0 auto 
            &>.maru_3:after
                margin 0 -1px 0 auto 
        .active-step>.maru:after 
            border-width 2px 
            border-color #f07
            background-color #fff
        &_faultline
            position absolute
            display block 
            content ""
            width 7rem
            height .08rem
            background-color #CDCACA 
            top .435rem
            z-index -1
            .progress
                height .08rem
                background-color #f07
</style>    



