<script>
export default {
    name:'detail-imgs',
    data(){
        return{
            show:-1,
            fakeData:['王老吉','光头强','李二蛋','张麻瓜','孙大圣']
        }
    },
    created(){
        setInterval(()=>{
            this.shuffle()
        },3000)
    },
    methods:{
        shuffle(){
            this.show = Math.floor(Math.random()*this.fakeData.length)
        }
    }
}
</script>
<template>
    <div class="detail-imgs">
        <Swiper
        :duration="300" 
        dots
        autoplay
        >
            <SwiperItem key="0">
                <div class="detail-imgs_img">
                1
                </div>
            </SwiperItem>
            <SwiperItem key="1">
                <div class="detail-imgs_img">
                2
                </div>
            </SwiperItem>
            <SwiperItem key="2">
                <div class="detail-imgs_img">
                3
                </div>
            </SwiperItem>
        </Swiper>
        <transition-group name="fake" mode="out-in">
            <template 
            v-for="(i,k) in fakeData"
            >
            <div 
            class="detail-imgs_fakedata"
            v-if="show === k"
            :key="k"
            >
                <img class="detail-imgs_fakedata_avatar" 
                src="http://mapi.fufentong.com/tmp/uploads/20190403/4ec7ca852438d82dd09dce0e0aab84a4.png"
                />
                <span>{{i}}刚刚下单成功</span>
            </div>
            </template>
        </transition-group>
    </div>
</template>
<style lang="stylus" scoped>
.detail-imgs 
    position relative
    width 7.5rem 
    height 7.5rem 
    &_img 
        width 7.5rem 
        height 7.5rem 
        background-color #ccc
    &_fakedata 
        position absolute 
        padding .1rem 
        padding-right .2rem
        background-color rgba(0,0,0,0.3)
        border-radius .4rem
        top .3rem 
        left .25rem 
        display flex 
        transform-origin 50% 50%
        &_avatar 
            display block
            width .4rem 
            height .4rem
            line-height .4rem 
        span 
            color #fff
            line-height .4rem 
            margin-left .1rem
            font-size .26rem
.fake-enter-active {
  animation: bounce-in .5s;
}
.fake-leave-active {
  animation: bounce-out .5s;
}
@keyframes bounce-in {
  0% {
    opacity 0
    transform: translate(0,100%) scale(.5);
  }
  100% {
    transform: translate(0,0%) scale(1);
    opacity 1
  }
}
@keyframes bounce-out {
  0% {
    transform: translate(0,0%) scale(1);
    opacity 1
  }
  100% {
    opacity 0
    transform: translate(0,-100%) scale(.5);
  }
}
</style>




