<script>
export default {
    name:'flash-sale-bar',
    data(){
        return {
            time:{
                hours:'00',
                minutes:'00',
                seconds:'00'
            }
        }
    },
    props:{
        start:{
            default:1561939200000,
            type:Number
        },
        end:{
            default:1564617600000,
            type:Number
        }
    },
    created(){
        this.countDown()
    },
    computed:{
        flash_sale_status(){
            let now = Date.parse(new Date());
            if(now>this.start&&now<this.end){
                return '距结束'
            }else if(now>this.end){
                return '已结束'
            }else{
                return '距开始'
            }
        }
    },
    methods:{
        countDown(){
            let now       =     Date.parse(new Date());
            // debugger
            let st = this.start;
            let et = this.end;
            let ElevenDay =     st>now?st:et;
            let times     =     (ElevenDay-now)/1000;
            let timer     =     setInterval(()=>{
            let day       =     0,
                hour      =     0,
                minute    =     0,
                second    =     0;
                if(times  >  0){
                    // day    = Math.floor(times / (60 * 60 * 24)).toString();
                    hour   = (Math.floor(times / (60 * 60))).toString();
                    minute = (Math.floor(times / 60)  - (day * 24 * 60) - (hour * 60)).toString();
                    second = (Math.floor(times)  - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60)).toString();
                }
                if (day   <= 9) day = '0' + day;
                if (hour   <= 9) hour = '0' + hour;
                if (minute <= 9) minute = '0' + minute;
                if (second <= 9) second = '0' + second;
                let data = {
                hours:hour,
                minutes:minute,
                seconds:second
                }
                this.time = data
                times--;
            },1000);
            if(times<=0){
                clearInterval(timer);
            }
        }
    }
}
</script>
<template>
    <div 
    class="flash-sale-bar"
    >
        <div class="flash-sale-bar_price">
            <div class="flash-sale-bar_oprice">￥299</div>
            <div class="flash-sale-bar_cprice">￥<span>199</span></div>
        </div>
        <div class="flash-sale-bar_stock">
            <div class="flash-sale-bar_stocknum">全场仅剩<span>99</span>件</div>
            <div class="flash-sale-bar_time">{{flash_sale_status}}:{{time.hours}}:{{time.minutes}}:{{time.seconds}}</div>
        </div>
    </div>
</template>
<style lang="stylus" scoped>
.flash-sale-bar 
    position relative
    height 1rem
    width 100%
    background-size cover
    background-image url(/static/imgs/falshsale@2x.png)
    padding 0 .25rem
    display flex 
    justify-content space-between
    &_price 
        display flex 
        flex-direction column
        justify-content center
        color #fff
    &_oprice 
        text-decoration line-through
    &_cprice>span 
        font-size .5rem
        font-weight bold
    &_stock 
        display flex 
        flex-direction column
        justify-content center
        text-align right
    &_stocknum 
        color #fff
        &>span 
            display inline-block
            background-color #fff
            color #f07
            padding 0 .03rem
            margin .05rem
            border-radius 2px
            line-height 1.2
    &_time 
        color #FFED00
        font-size .2rem
        margin-top .1rem
</style>




