<script>
export default {
    name:'bar',
    methods:{
        buyNow(){
            this.ActionSheetStatus = true;
            
            // this.bus.$emit('checkphone',true)
        },
        ActionSheetOff(){
            this.ActionSheetStatus=false
        },
        goto(){
            this.$router.push({
                name:'table'
            })
        }
    },
    data(){
        return{
            ActionSheetStatus:false
        }
    }
}
</script>
<template>
    <div class="flash-order-bar">
        <div class="flash-order-bar_home">
            <i class="iconfont icon-home" style="font-size:.4rem"></i>
            <span style="font-size:0.2rem">首页</span>
        </div>
        <div class="flash-order-bar_buybtn"
        @click="buyNow"
        >立即购买</div>
        <ActionSheet
        @close="ActionSheetOff"
        :status="ActionSheetStatus"
        hideCancel
        >
            <div class="product_standard">
                <div class="product_info">
                    <div class="product_info_img">
                        <img src="http://mapi.fufentong.com/tmp/uploads/20190418/d59a1649e550d19d34ea55418940794f.jpg" alt="">
                    </div>
                    <div class="product_info_combine">
                        <span 
                        style="font-size:.32rem;margin-bottom:.2rem"
                        >歌德小麦啤酒500ml*12歌德小麦啤酒500ml*12*12</span>
                        <span>已选:大瓶,500ml</span>
                    </div>
                </div>
                <div class="product_format">
                    <div class="product_format_tag">规格</div>
                    <div class="product_format_sort">
                        <div class="sort-item">粉色</div>
                        <div class="sort-item">黄色</div>
                        <div class="sort-item">灰色</div>
                    </div>
                </div>
                <div class="product_buy"
                @click="goto"
                >
                    立即购买
                </div>
            </div>
        </ActionSheet>
    </div>
</template>
<style lang="stylus" scoped>
.flash-order-bar
    position fixed
    height 1rem
    width 100%
    bottom 0 
    left 0 
    z-index 99
    display flex
    background-color #fff
    &_home 
        width 1.2rem 
        display flex
        flex-direction column
        justify-content center
        text-align center
        color #9C969A
    &_buybtn 
        flex 1 
        line-height 1rem
        text-align center
        font-size .32rem 
        color #fff
        background: linear-gradient(90deg, #fe44ba 0%, #ff0b82 100%)
.product_standard
    padding 1.25rem .25rem .25rem
    position relative
    height 5rem
    .product_info
        width 7rem
        position absolute
        display flex 
        top -.8rem
        &_img 
            width 1.8rem 
            height 1.8rem 
            box-shadow 2px 2px 5px rgba(0,0,0,0.6)
            img 
                width 100%
                height 100%
        &_combine
            flex 1
            display flex 
            flex-direction column
            justify-content flex-end
            padding 0 .25rem
            text-align left 
            &>span 
                width 4.7rem
                white-space nowrap
                overflow hidden
                text-overflow ellipsis
    .product_format
        position relative
        height 2.8rem
        &_tag 
            text-align left 
            font-size .28rem
            font-weight bold 
            line-height 1.6
        &_sort 
            display flex 
            .sort-item 
                padding .15rem .3rem
                min-width 2rem
                margin-right .2rem 
                margin-bottom .2rem
                border-radius .1rem
                border 1px solid #666
    .product_buy
        position absolute
        font-size .32rem
        color #fff 
        width 100%
        height 1rem 
        line-height 1rem
        bottom 0 
        left 0
        background linear-gradient(136deg,rgba(254,75,193,1) 0%,rgba(255,0,119,1) 100%);
</style>



